'use strict';

// create the module and name it scotchApp
angular.module('mounirApp', ['ui.router', 'ngAnimate', 'duScroll'])
  .config(routesConfig)
  .value('duScrollEasing', easeInOutQuad)
  .run(run);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider

    // HOME STATES AND NESTED VIEWS ========================================
    .state('home', {
      url: '/',
      templateUrl: 'app/partials/home.html',
    })

    // PROJECTS PAGE AND MULTIPLE NAMED VIEWS =================================
    .state('projects', {
      url: '/projects',
      abstract: true,
      templateUrl: 'app/partials/projects.html',
      resolve: {
        projects: function (ProjectsService) {
          return ProjectsService.getAllProjects();
        }
      }
    })

    .state('projects.list', {
      url: '/list',
      templateUrl: 'app/partials/projects.list.html',
      controller: 'ProjectsController'
    })
    // INDIVIDUAL PROJECT PAGE =================================
    .state('projects.details', {
      url: '/:projectId',
      templateUrl: 'app/partials/project.details.html',
      controller: 'ProjectController',
      resolve: {
        project: function (ProjectsService, $stateParams) {
          return ProjectsService.getProject($stateParams.projectId);
          console.log('STATE PARAMS' + $stateParams.projectId);

        }
      }
    })

    // ABOUT PAGE AND MULTIPLE NAMED VIEWS =================================
    .state('about', {
      url: '/about',
      controller: 'AboutController',
      views: {
        // the main template will be placed here (relatively named)
        '': {
          templateUrl: 'app/partials/about.html'
        },
        // the child views will be defined here (absolutely named)
        'aboutDetails@about': {
          templateUrl: 'app/partials/about.details.html'
        },
      }
    })

    // CONTACT PAGE AND MULTIPLE NAMED VIEWS =================================
    .state('contact', {
      url: '/contact',
      views: {
        // the main template will be placed here (relatively named)
        '': {
          templateUrl: 'app/partials/contact.html'
        },
        // the child views will be defined here (absolutely named)
        'contactForm@contact': {
          templateUrl: 'app/partials/contact.form.html',
          controller: 'ContactsController'
        },
      }
    });

}

/** @ngInject */
function easeInOutQuad(t) {
  return t < .5 ? 2 * t * t : -1 + (4 - 2 * t) * t;
}

/** @ngInject */
function run($rootScope, $location, $window, $http) {
  $rootScope.$on('$stateChangeSuccess', function () {
    FastClick.attach(document.body);
    $window.ga('send', 'pageview', $location.path());

    var body = angular.element(document.querySelector('body'));
    // console.log(body);

    //  scroll on body
    var hasScrolled = false;
    angular.element(window).bind("scroll", function () {
      if (this.pageYOffset >= 100) {
        // console.log('Scrolled below header.');
        // console.log(body);
        body.removeClass('scrolled-out').addClass('scrolled');
        hasScrolled = true;
        // console.log(body);

      } else {
        // console.log('Header is in view.');
        body.removeClass('scrolled');
        if (hasScrolled) {
          body.addClass('scrolled-out');
        }
      }
      // scope.$apply();
    });

  });

  $http.get('app/data/projects.json', {
    cache: true
  });

}

'use strict';

angular.module('mounirApp')
  .factory('ProjectsService', ProjectsService);

/** @ngInject */
function ProjectsService($http) {
  var service = {
    getAllProjects: function() {
      return $http.get('app/data/projects.json', { cache: true }).then(function(resp) {
        return resp.data;
      });
    },
    
    getProject: function(id) {
      function projectMatchesParam(project) {
        return project.id === id;
      }
      
      return service.getAllProjects().then(function (projects) {
        return projects.find(projectMatchesParam);
      });
    }
  };
  
  return service;

}

(function () {
  'use strict';

  angular
    .module('mounirApp')
    .directive('siteNavbar', siteNavbar)
    .directive('siteFooter', siteFooter);

  /** @ngInject */
  function siteNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/partials/header.html',
      controller: siteNavbarController,
      controllerAs: 'menu'
    };

    return directive;

    /** @ngInject */
    function siteNavbarController() {
      var vm = this;
      //   console.log('Directive siteNavbar loaded...');
      vm.showNavbar = false;

      vm.toggleNavbar = function () {
        if (!vm.bodyClass) {
          vm.bodyClass = 'nav-open';
        } else {
          vm.bodyClass = '';
        }

        vm.showNavbar = !vm.showNavbar;
      };
    }
  }


  /** @ngInject */
  function siteFooter() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/partials/footer.html',
      controller: siteFooterController,
      controllerAs: 'footer'
    };

    return directive;

    /** @ngInject */
    function siteFooterController() {
      var vm = this;
      vm.year = 2017;
        console.log('Directive siteFooter loaded...');
    }
  }


})();

(function () {
  'use strict';

  angular.module('mounirApp')
    .controller('MainController', MainController)
    .controller('ProjectsController', ProjectsController)
    .controller('ProjectController', ProjectController)
    .controller('AboutController', AboutController)
    .controller('ContactsController', ContactsController);

  /** @ngInject */
  function ProjectsController($scope, projects) {
    console.log('ProjectsController loaded...');
    // $scope.message = 'About!';

    $scope.pageClass = 'projects-page';

    $scope.projects = projects;

    console.log("----- LIST CONTROLLER LAUNCHED... ----");
    console.log(projects);
  }

  /** @ngInject */
  function ProjectController($scope, project, $timeout, $anchorScroll) {
    console.log('ProjectController loaded...');

    $scope.pageClass = 'project-page';

    $timeout(function () {
      $anchorScroll('section-projects');
    });

    console.log("----- DETAILS CONTROLLER LAUNCHED... ----");
    $scope.project = project;
    console.log(project);
  }

  /** @ngInject */
  function AboutController($scope) {
    console.log('about loaded...');

    $scope.message = 'About!';
    $scope.pageClass = 'about-page';
  }

  /** @ngInject */
  function ContactsController($scope, $http) {
    console.log('Contact Controller loaded...');

    $scope.message = 'Contact';
    $scope.pageClass = 'contact-page';

    $scope.loading = false;
    $scope.send = function (mail) {
      $scope.loading = true;
      $http.post('/send', {
        from: mail.from,
        name: mail.name,
        message: mail.message
      }).then(function (res) {
        $scope.loading = false;
        $scope.serverMessage = 'Email sent successfully';
        console.log('RES...' + res);
      });
    };

  }

  /** @ngInject */
  function MainController($scope) {
    console.log('main loaded...');

    $scope.test = "World";
  }

})();

